body {
  --main-color: #00acdc;
  margin: 0;
  font-family: Roboto, serif;
}

.header {
  background-color: #fff;
  justify-content: flex-end;
  display: flex;
  position: relative;
}

.logo {
  position: absolute;
  top: 10px;
  left: 50px;
}

.nav {
  text-transform: uppercase;
  align-items: center;
  margin-right: 50px;
  font-size: 12px;
  font-weight: 500;
  line-height: 60px;
  display: flex;
}

.nav__link {
  display: inline-block;
}

.nav__link:not(:first-child) {
  margin-left: 20px;
}

.nav__link:hover {
  color: var(--main-color);
}

.is-active {
  color: var(--main-color);
  position: relative;
}

.is-active:after {
  content: "";
  background-color: var(--main-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}
